.capapretaeditcss{
    width: 50%;
    margin-top: 30px;
    
}



.skillseditcss, .portifolioeditcss{
    background-color: rgb(2,0,36,100);
    height: 900px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center ;
    
}


.divapresentacao{
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
}

.imagemapresentacao{
    height: 50%;
    width: 50%;
}


.textoapresentacao{

    height: 50%;
    width: 50%;
    text-align: center;
    
}

.textoapresentacao h2{
    color: #fff;
    margin-bottom: 50px;
    font-family: 'Bradley Hand', cursive;
    font-size: 50px;
    font-weight: bold;
}

.textoapresentacao p {
    font-family:'Poppins', sans-serif ;
    font-size: 30px;
    font-style: italic;
    color: #fff;
    margin-right: 10px;
}

.imagemelefante{
    height: 100%;
    width: 100%;
}

@media (max-width: 768px){
    .divapresentacao{
        flex-direction: column;
    }

    .textoapresentacao p{
        font-size: 15px;
    }

    .textoapresentacao h2{
        font-size: 30px;
        margin-top: 100px;
        margin-bottom: 20px;
        text-align: center;
        color: rgb(31, 2, 52, 0.5);
    }

    .telefone{
        font-weight: bold;
        justify-content: center;
        align-items: center;
        font-family: 'Poppins', sans-serif;
        
    }

  

    .nomesicones{

        font-weight: bold;
        font-family: 'Poppins', sans-serif;
    }
    .imagemapresentacao{
        height: 70%;
        width: 70%;
    }

    .cornavegacao{
        position: absolute;
        width: 100%;
       
    }

    .iconesdonavbar{
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .iconesdonavbar .icones{
        margin-right: 10px;
        
    }

    
}