.footerpai{

    height: 500px;
    width: 100%;
    background-color: rgb(2,0,36,100);
   
}

.footerfilho{

    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    height: 100%;
    width: 100%;
}


.icone{
    color: #fff;
    font-size: 50px;
}

.iconeinsta{
   
   margin-bottom: 370%;
   margin-left: 30%;
}

.iconeinsta:hover{
   color: rgb(253, 29, 29, 100);
}

.iconelinkedin{
    margin-bottom: 230%;
}

.iconelinkedin:hover{
    color: #0e76a8;
}

.iconegithub{
    margin-bottom: 300%;
}

.iconegithub:hover{
    color: #000;
}

.iconephone{
   font-size: 45px;
   color: #fff;
   margin-bottom: 100%; 
}

.telefonespan{
   margin-right: 20%;
   margin-bottom: 30%;
   font-size: 30px;
   color: #fff;
}

@media (max-width: 768px){

    .icone{
        font-size: 40px;
    }

    .iconephone{
        font-size: 40px;
    }
    
    .telefonespan{
        font-size: 25px;
    }
}


@media (max-width: 411px){

   

    .icone{
        font-size: 30px;

    }

    .iconephone{
        font-size: 30px;
        margin-bottom: 450%;
    }
    
    .telefonespan{
        font-size: 15px;
        margin-bottom: 100%;
    }

    .iconeinsta{
        margin-bottom: 500%;
    }

    .iconelinkedin{
        margin-bottom: 700%;
    }

    .iconegithub{
        margin-bottom: 500%;
    }



}
