.velhoeditcss{
    height: 90%;
    width: 100%;
}


.garotoeditcss{
    height: 80%;
    width: 50%;
}

.textoeditcss{
    height: 90%;
    width: 100%;
    color: #fff;
    text-align: center;
    margin-top: 270px;

}
.texto2editcss{
    height: 70%;
    width: 100%;
    color: #fff;
    text-align: center;
    display: flex;
    flex-direction: column;
    
}
.divdoyt{
    width: 100%;
    height: 100%;
}
.textodovideo{
    width: 100%;
    
}

.iconeslinguas{
    height: 100px;
    width: 100px;
    margin: 50px;

}

.iconeslinguas:hover{
    color: rgb(185,6,150,100);
}

.giteditcss{
    height: 100px;
    width: 100px;
}

@media (max-width: 768px){

    .velhoeditcss{
        height: 70%;
        width: 60%;
    }

    .garotoeditcss{
        height: 50%;
        width: 50%;
        margin-top: 50px;
    }

   
    .skillseditcss{
        display: flex;
        flex-direction: column;
    }

    .portifolioeditcss{

        display: flex;
        flex-direction: column-reverse;
        height: 40%;
    }

    .textoeditcss{
        height: 50%;
        width: 100%;
        color: #fff;
        text-align: center;
        margin-top: 20px;
    
    }

    .texto2editcss{
        height: 50%;
        width: 100%;
        color: #fff;
        text-align: center;
        display: flex;
        flex-direction: column;
        margin-top: 50px;
        
    }

    .iconeslinguas{
        height: 80px;
        width: 80px;
        margin: 40px;
    
    }

}

@media (max-width: 500px){

    
    .velhoeditcss{
        height: 50%;
        width: 100%;
        margin: 0;
    }

    .garotoeditcss{
        height: 70%;
        width: 70%;
        margin-top: 50px;
    }

   
    .skillseditcss{
        display: flex;
        flex-direction: column;
        
    }

    .portifolioeditcss{

        display: flex;
        flex-direction: column-reverse;
        height: 40%;
    }

    .textoeditcss{
        height: 50%;
        width: 100%;
        color: #fff;
        text-align: center;
        margin-top: 80px;
        
    }

    .texto2editcss{
        height: 50%;
        width: 100%;
        color: #fff;
        text-align: center;
        display: flex;
        flex-direction: column;
        margin-top: 50px;
        
    }

    .divdoyt{
        width: 100%;
    }

    .videoyt iframe {

        width: 100%;
    }
  

    .giteditcss{
        width: 80px;
    }

    .textodovideo{
        font-size: 20px;
    }

    .iconeslinguas{
        height: 50px;
        width: 50px;
        margin: 25px;
    
    }

}
