.telefone{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
   
    
}

.iconessociais{

    margin-left: 30px;
}



.iconenoite{
    background-image: url('../../../public/assets/noite.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    width: 20px;
    height: 20px;
    border-radius: 50% 50%;
}

.iconedia{
    background-image: url('../../../public/assets/dia.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    width: 20px;
    height: 20px;
    border-radius: 50% 50%;
    margin-left: 2px;
}

.iconebotao{
    
   background-color: #FFF;
    width: 21px;
    height: 21px;
    border-radius: 50% 50%;
    position: absolute;
    cursor: pointer;
    transform: translateX(0px);
    transition: transform 0.2 linear;
       
}

.iconebotao.moved{
    transform: translateX(20px);
}



.iconetrocadecor{
    display: flex;
    flex-direction: row;
    
}

.nomesicones{
    cursor: pointer;
}



